import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home.vue'
import Diary from '@/components/Diary.vue'
import AddDia from '@/components/AddDia.vue'
import SingDiary from '@/components/SingDiary.vue'
import Login from '@/components/login.vue'
import Register from '@/components/register.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {path : '/', name : 'Home',component:Home},
    {path : '/diary', name : 'Diary', component : Diary},
    {path : '/addDiary', name　: 'addDiary', component : AddDia},
    {path : '/singDiary/:id', name　: 'singDiary', component : SingDiary},
    {path : '/user/login', name　: 'Login', component : Login},
    {path : '/user/register', name　: 'Register', component : Register},
  ],
  mode : 'history',
  scrollBehavior(to,from,savedPosition){
    if (savedPosition){
      return savedPosition
    }else{
      return {x: 0 , y: 0}  
    }
  }

})



