<template>
    <div id="Diary">
        <div class="diaForm">
            <form action="">
                <input type="search" class="diaSearch" v-model="search"  placeholder="输入要查找的标题" required>       
            </form>
            <!-- v-if="level ==2 " -->
            <p v-if="UserCookie.level == 2"><router-link :to="{name : 'addDiary'}">发表</router-link></p>
        </div>
        <div class="diaWrap" v-for="dir in filterdirs" :key="dir.index">
                <h2 v-color>{{dir.title}}</h2>
                <div>
                    <span>{{dir.author}}</span>
                    <span>{{dir.updatedAt}}</span>   
                </div>
            <router-link v-bind:to="'/singDiary/'+dir.objectId"><article>{{dir.content}}</article></router-link>
        </div>
    </div>
</template>

<script>
export default {
    name : 'Diary',
    props:{
        UserCookie : {
            type : Object,
            default : true
        }
    },
    data(){
        return {
            dirs : [],
            search : '',
            level : '0'
        }
    },
    created(){
        const query = Bmob.Query("Diary");
        query.find().then(res => { 
            this.dirs = res;
        });
        // if(Bmob.User.current()){
        //     let current = Bmob.User.current()
        //     this.level = current.level
        // }
        
    },
    computed : {
        filterdirs:function () {
            return this.dirs.filter((dirs)=>{  //fiter 用来获取符合条件的值
                return dirs.title.match(this.search)    
            })
        }
    },
    directives : {
        'color' : {
            bind(el,binding,vnode){
                el.style.color = `#${Math.random().toString(16).slice(2,8)}`
            },
        }
    }
}
</script>

<style scoped>
    #Diary{
        min-width: 1024px;
        max-width: 1199px;
        margin: 15px auto;
    }
    #Diary .diaForm{
        margin: 0 auto;
    }
    #Diary .diaForm p{
        width: 55px;
        font-size: 16px;
        text-align: center;
        background: yellowgreen;
        cursor: pointer;
        
    }
    #Diary .diaForm form{
        position: relative;
        width: 500px;
        margin: 0 auto;
    }
    #Diary input[type="search"]{
        /* padding: 8px;
        box-sizing: border-box;
        width: 796px;
        height: 46px;
        line-height: 50px;
        font-size: 21px; */
        width: 100%;
        height: 42px;
        padding-left: 13px;
        border: 2px solid #7BA7AB;
        border-radius: 5px;
        background: #F9F0DA;
        color: #9E9C9C;
        outline: none;
    } 

    
    
    .diaWrap{
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #fff;
        cursor: pointer;
    }
    .diaWrap h2{
        text-align: center;
    }
    .diaWrap div{
        text-align: center;
    }
    .diaWrap div span{
        margin: 0 20px;
    }
    .diaWrap a{
        color: #444;
        text-decoration: none;
    }
    
    /* 
        日记: 标题 内容 时间 
    
    */
</style>




