<template>
  <div class="main">
    <div class="header">
      <router-link :to="{name : 'Home'}">
        <img src="./../assets/logo-1.png" alt="">
      </router-link>
      <h1>用户注册</h1>
    </div>
    <form id="">
      <div class="form-group">
        <label for="email">邮箱</label>
        <input type="email" v-model='user.email' class="form-control" id="email" name="email" placeholder="Email" autofocus>
      </div>
      <div class="form-group">
        <label for="nickname">昵称</label>
        <input type="text" v-model='user.username' class="form-control" id="nickname" name="nickname" placeholder="Nickname">
      </div>
      <div class="form-group">
        <label for="password">密码</label>
        <input type="password"  v-model='user.password' class="form-control" id="password" name="password" placeholder="Password">
      </div>
      <button class="btn btn-success btn-block" @click.prevent="post">注册</button>
    </form>
    <div class="message">
      <p>已有账号? <router-link :to="{name : 'Login'}" >点击登录</router-link>.</p>
    </div>
    <p>{{user.email}}</p>
  <p>{{user.username}}</p>
  <p>{{user.password}}</p>
  </div>
  
</template>

<script>
export default {
  name : 'reg-user',
  data(){
    return{
      user :{
        username : '',
        password : '',
        email : ''
      }
    }
  },
  methods: {
      post(){
        
        Bmob.User.register(this.user).then(res => {
          console.log(res)
          alert('恭喜你注册成功 赶紧去登录吧~')
          window.location.href = "/user/Login"
        }).catch(err => {
          console.log(err)
          alert('很遗憾注册失败 请重试')
        });
      }
    }
}
</script>

<style scoped>
 @import './../../static/css/bootstrap.min.css';   
</style>
<style scoped>
body {
  background-color: #f9f9f9;
}

.main {
  width: 340px;
  margin: 0 auto;
  margin-top: 50px;
  color: #333;
}

.main .header {
  text-align: center;
}

.main .header h1 {
  font-size: 26px;
}

.main form {
  padding: 20px;
  margin-bottom: 15px;
  border: 1px solid #d8dee2;
  border-radius: 5px;
  background-color: #fff;
}

.main .message {
  padding: 10px;
  padding-bottom: 0;
  border: 1px solid #d8dee2;
  border-radius: 5px;
}

</style>




