<template>
  <div class="main">
    <div class="header">
      <router-link :to="{name : 'Home'}">
        <img src="./../assets/logo-1.png" alt="">
      </router-link>
      <h1>用户登录</h1>
    </div>
    <form id="login_form">
      <div class="form-group">
        <label for="">邮箱</label>
        <input type="email" class="form-control" id="" v-model="email" name="email" placeholder="Email" autofocus>
      </div>
      <div class="form-group">
        <label for="">密码</label>
        <a class="pull-right" href="">忘记密码？</a>
        <input type="password" v-model="password"  class="form-control" id="" name="password" placeholder="Password">
      </div>
      <div class="checkbox">
        <label>
          <input type="checkbox">记住我
        </label>
      </div>
      <button type="button" class="btn btn-success btn-block" @click.prevent="post">登录</button>
    </form>
    <div class="message">
      <p>没有账号? <router-link :to="{name : 'Register'}">点击创建</router-link>.</p>
    </div>
  </div>
</template>

<script>
export default {
  name : 'login-user',
  data(){
    return{
      email : '',
      password : ''
    }
  },
  methods: {
    post(){
       Bmob.User.login(this.email,this.password).then(res => {
       
        window.location.href = "/"
      }).catch(err => {
        alert('登录失败~')
      });
      
    }
  }
}
</script>

<style scoped>
 @import './../../static/css/bootstrap.min.css';   
</style>

<style scoped>
body {
  background-color: #f9f9f9;
}

.main {
  width: 340px;
  margin: 0 auto;
  margin-top: 50px;
  color: #333;
}

.main .header {
  text-align: center;
}

.main .header h1 {
  font-size: 26px;
}

.main form {
  padding: 20px;
  margin-bottom: 15px;
  border: 1px solid #d8dee2;
  border-radius: 5px;
  background-color: #fff;
}

.main .message {
  padding: 10px;
  padding-bottom: 0;
  border: 1px solid #d8dee2;
  border-radius: 5px;
}

</style>



