<template>
    <div id="Sing-Diary">
        <ul>
            <li>
                <h2>标题:</h2>
                <p v-if="state">{{diarys.title}}</p>
                <input autopostback = 'false' type="text" v-else v-model="diarys.title">
            </li>
            <li>
                <h2>内容:</h2>
                <p v-if="state">{{diarys.content}}</p>
                <textarea autopostback = 'false' v-else v-model="diarys.content"></textarea>
                
            </li>
            <li>
                <button v-if="state" @click="bj()">编辑</button>
                <button v-else @click="enter">确认</button>
                <button @click="del">删除</button>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name : 'Sing-Diary',
    data(){
        return{
            diarys : {},
            state : true,
            state2 : false,
            objectID : new String(this.$route.params.id),
            
        }
    },

    methods : {
        ajx : function(){
            const query = Bmob.Query('Diary')
            query.get(this.objectID).then(res => {
                this.diarys = res;
            }).catch(err => {
                console.log(err)
            }) 
        },
        bj: function(){
            if(this.state==true){
                this.state=false;
            }else{
                this.state=true
            }
        },
        enter : function(){
            if(this.state==true){
                this.state=false;
            }else{
                this.state=true
            }     
            const query = Bmob.Query('Diary');
            query.get(this.objectID).then(res => {
                res.set('content',this.diarys.content)
                res.set('title',this.diarys.title)
                res.save()
            }).catch(err => {
                console.log(err)
            })               
            window.location.href = "/diary"
        },
        del : function(){
            const query = Bmob.Query('Diary');
            query.destroy(this.objectID).then(res => {
                console.log(res)
            }).catch(err => {
                console.log(err)
            })
            window.location.href = "/diary"
        }
    },
    created(){
        this.ajx()
    },
}
</script>

<style scoped>
#Sing-Diary *{
    box-sizing: border-box;
}
#Sing-Diary{
    margin: 20px auto;
    padding: 20px;
    min-width: 600px;
}
p{  
    line-height: 50px;
    background: #ccc;
}
h2{
    color: yellowgreen
}

input[type='text'],textarea,select{
    display: block;
    padding: 8px;
    width: 100%;
}
textarea{
    height: 200px;
    border:0;
    border-radius:5px;
    background-color:rgba(241,241,241,.98);
    /*width: 355px;*/
    padding: 10px;
    resize: none;
    font-size: 18px;
}

button{
    display: block;
    margin-top: 10px;
    width: 270px; /* 宽度 */
	height: 40px; /* 高度 */
	border-width: 0px; /* 边框宽度 */
	border-radius: 3px; /* 边框半径 */
	background: #1E90FF; /* 背景颜色 */
	cursor: pointer; /* 鼠标移入按钮范围时出现手势 */
	outline: none; /* 不显示轮廓线 */
	font-family: Microsoft YaHei; /* 设置字体 */
	color: white; /* 字体颜色 */
	font-size: 17px; /* 字体大小 */
}
</style>



