<template>
    <div id="add-dia">
        <h1>写日记</h1>
        <div>
            <form action="">
                <label for="">标题</label>
                <input type="text" v-model="dia.title">
                <label for="">作者</label>
                <select>
                    <option value="">噫嘻</option>
                </select>
                <label for="">内容</label>
                <textarea v-model="dia.content">

                </textarea>
                <button @click.prevent="post">发表</button>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name : 'add-dia',
    data(){
        return {
            dia:{
                title : '',
                content : '',
                author : '噫嘻'
            }
        }
    },
    methods:{
        post(){
            const Diary = Bmob.Query('Diary');
            Diary.set('title',this.dia.title)
            Diary.set('content',this.dia.content)
            Diary.set('author',this.dia.author)
            
            
            Diary.save().then(res => {
                console.log(res)
            }).catch(err => {
                console.log(err)
            })
        }
    }
}
</script>

<style scoped>
#add-dia *{
    box-sizing: border-box;
}
#add-dia{
    margin: 20px auto;
    padding: 20px;
    min-width: 600px;
    
}

label{
    display: block;
    margin: 20px 0 10px;
}
input[type='text'],textarea,select{
    display: block;
    padding: 8px;
    width: 100%;
}
textarea{
    height: 200px;
    border:0;
    border-radius:5px;
    background-color:rgba(241,241,241,.98);
    /*width: 355px;*/
    padding: 10px;
    resize: none;
    font-size: 18px;
}

button{
    display: block;
    margin-top: 10px;
    width: 270px; /* 宽度 */
	height: 40px; /* 高度 */
	border-width: 0px; /* 边框宽度 */
	border-radius: 3px; /* 边框半径 */
	background: #1E90FF; /* 背景颜色 */
	cursor: pointer; /* 鼠标移入按钮范围时出现手势 */
	outline: none; /* 不显示轮廓线 */
	font-family: Microsoft YaHei; /* 设置字体 */
	color: white; /* 字体颜色 */
	font-size: 17px; /* 字体大小 */
}
</style>



