<template>
  <div id="App-Header">
    <header id="header">
        <nav>
          <ul class="header-left">
            <div class="logo"><a href="http://www.yangqq.com">噫嘻JS个人博客</a></div>
            <li><router-link :to="{name:'Home'}">主页</router-link></li>
            <li><router-link :to="{name:'Diary'}">日记</router-link></li>
            <li><router-link :to="{name:'Home'}">交流</router-link></li>
            <li><router-link :to="{name:'Home'}">留言</router-link></li>
            <li><router-link :to="{name:'Home'}">关于我</router-link></li>
          </ul>
          <ul v-if="state1" class="header-right">
            <li><router-link :to="{name:'Register'}">注册</router-link></li>
            <li><router-link :to="{name:'Login'}">登录</router-link></li>
          </ul>
          <ul v-else class="header-right">
            <li @click.prevent="edit"><router-link :to="{name:'Home'}">退出登录</router-link></li>            
            <li><router-link :to="{name: 'Home'}">欢迎您!{{UserCookie.username}}</router-link></li>
            
          </ul>
        </nav>
    </header>
  </div>
</template>

<script>
export default {
  name: 'App-Header',
  props:{
    UserCookie : {
      type : Object,
      default : true
    }
  },
  data () {
    return {
      state1 : true
    }
  },
  
  methods:{
    edit(){
      Bmob.User.logout()
      this.state1 = true
    },
    
  },
  created(){
    //let current = Bmob.User.current()
      //顺便控制登录 的隐藏和显示
    if(this.UserCookie){
      
      this.state1 = false
    }
    //console.log(Bmob.User.current())
  }
  
}
</script>

<style scoped>
  #App-Header{
    
  }
  #header{
    padding: 5px 0;
    width: 100%;
    background: #1C2327;
    height: 50px;
  }
  #header::before { background: #000 linear-gradient(to left, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55); content: ""; height: 5px; position: absolute; top: 0; width: 100%; }
  nav{
    min-width: 1024px;
    max-width: 1199px;
    margin: 0 auto;
        line-height: 50px;
    text-align: center;
  }
  .header-left li{
    position: relative;
    display: inline;
    float: left;
    width: max-content;
  }
  #header li a{
    display: inline;
    padding: 0 30px;
    color: #fff;
  }
  #header li a:hover{
    color : #00c1de;
  }
  .logo{
    margin: 0;
    margin-right: 50px;
    float: left;
    line-height: 50px;
    font-size: 22px;
    color: #FFF;
  }
  .logo a{
    color: #FFF
  }
  .header-right li{
    position: relative;
    display: inline;
    float: right;
    width: max-content;
  }
</style>



