<template>
  <div id="app">
    <HeaderBlog v-bind:UserCookie="UserCookie"></HeaderBlog>
    <router-view v-bind:UserCookie="UserCookie"></router-view>
  </div>
</template>

<script>
import HeaderBlog from './components/Header'
import HomeBlog from './components/Home'
import Diary from './components/Diary'
export default {
  name: 'App',
  data(){
    return {
      UserCookie : ''
    }
  },
  components : {
    HeaderBlog,
    HomeBlog,
    Diary,
  },
  created(){
    let current = Bmob.User.current()
    console.log(Bmob.User.current())
    this.UserCookie = current
  }
}
</script>

<style>
  * { margin: 0; padding: 0 }
  body { font: 15px "Microsoft YaHei", Arial, Helvetica, sans-serif; color: #333; background: #E9EAED; line-height: 1.5; overflow-x: hidden; }
  img { border: 0; display: block }
  ul, li { list-style: none; }
  a { text-decoration: none; color: #333; }
  a:hover { color: #000; text-decoration: none; }
</style>



